<template>
  <div id="app">
    <div class="poster">
      <p>海報</p>
      <img :src="base64" alt="" />
      <div id="qrcode"></div>
    </div>

    <button @click="download">download</button>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";
import { domToPng } from "modern-screenshot";
import html2canvas from "html2canvas";
import { base64 } from "./img";
export default {
  name: "App",
  components: {},
  data() {
    return {
      dataUrl: "",
      base64,
    };
  },
  computed: {},
  watch: {},
  created() {},
  async mounted() {
    new QRCode("qrcode", {
      text: "http://jindo.dev.naver.com/collie",
      width: 128,
      height: 128,
      colorDark: "#000000",
      colorLight: "#ffffff",
      correctLevel: QRCode.CorrectLevel.H,
    });

    // this.dataUrl = await domToPng(document.querySelector(".poster"), {
    //   fetch: {
    //     requestInit: {
    //       mode: "cors",
    //     },
    //   },
    // });
    this.dataUrl = await html2canvas(document.querySelector(".poster"), {
      useCORS: true,
      allowTaint: true,
    }).then((canvas) => {
      return canvas.toDataURL("image/png");
    });
    console.error(this.dataUrl);
  },
  beforeDestroy() {},
  methods: {
    download() {
      const link = document.createElement("a");
      link.download = "poster.png";
      link.href = this.dataUrl;
      link.click();
    },
  },
};
</script>

<style lang="less">
* {
  margin: 0;
}
.clamp {
  display: -webkit-box;
  line-clamp: 3;
  -webkit-line-clamp: 3; /* 限制显示的行数 */
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.poster {
  position: relative;
  width: 200px;
  height: 200px;
  background-color: antiquewhite;
  #qrcode {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}
</style>
